<template>
	<div>
		<ul class="nav nav-tabs flex-shrink-0">
			<li class="nav-item" v-for="(tabObj, index) in tabList" :key="tabObj.id">
				<a
					href="javascript:void(0);"
					:class="{ 'nav-link': true, active: selectedIdx === index }"
					@click="changeTab(index)"
					v-text="tabObj.label"
				>
				</a>
			</li>
		</ul>
		<div class="panel-body tab-content" style="height: 43vh">
			<div :class="{ [modalMode]: true, 'tab-pane': true, active: selectedIdx === idx }" v-for="(detailInfo, idx) in tabList" :key="idx">
				<DetailBasic
					ref="component"
					:detailInfo="detailInfo"
					:detailData="userInfoData"
					:mode="modalMode"
					v-if="'basic' === detailInfo.type"
				/>
				<div class="form-table form-table-responsive" v-else-if="'checkboxProperty' === detailInfo.type">
					<div class="form-table-head">
						<label class="col-lg-1">{{ $t('label.user.privilege.grade') }}</label>
						<label class="col-lg-1">{{ $t('label.privilege.type') }}</label>
						<label class="col-lg-8">{{ $t('label.privilege') }}</label>
					</div>
					<div class="form-table-wrap userInfo">
						<div class="form-table-body" v-for="(propertyRow, propertyIndex) in totalUserPrivileges" :key="propertyIndex">
							<div class="col-lg-2">
								<div style="width: 100%">
									<select
										v-model="propertyRow.privilegeName"
										class="form-control view-disabled"
										:val="(propertyRow.privilegeName = propertyRow.admin ? 'Admin' : propertyRow.member ? 'Member' : 'None')"
										:readonly="true"
									>
										<option value="None">{{ $t('label.privilege.none') }}</option>
										<option value="Admin">{{ $t('label.privilege.admin') }}</option>
										<option value="Member">{{ $t('label.privilege.member') }}</option>
									</select>
								</div>
							</div>
							<div class="col_lg_2">
								<div style="width: 100%">
									<inputText
										vModel="privilegeType"
										:classObject="{ 'form-control': true, 'view-disabled': true }"
										:val="
											propertyRow.privilegeType === isSystem
												? $t('label.privilege.type.system')
												: propertyRow.privilegeType === isBusiness
												? $t('label.privilege.type.business')
												: $t('label.role')
										"
										:readonly="true"
									/>
								</div>
							</div>
							<div class="col-lg-8">
								<div style="width: 100%">
									<inputText
										vModel="privilege"
										:classObject="{ 'form-control': true, 'view-disabled': true }"
										:val="propertyRow.privilegeId"
										:readonly="true"
									/>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import merge from 'lodash/merge';
import { HttpReq, changeDateFormat } from '@/utils/common.js';
import { mapState } from 'vuex';
import DetailBasic from '@/components/detail/DetailBasic';
import inputText from '@/components/element/inputText';
import cloneDeep from 'lodash/cloneDeep';
import { encryptPassword } from '@/utils/common';

export default {
	name: 'userInfoModal',
	components: {
		DetailBasic,
		inputText
	},
	computed: {
		...mapState(['userInfo'])
	},
	watch: {
		modalMode() {
			this.getData(() => {
				this.totalUserPrivileges = this.userInfoData.totalUserPrivileges;

				this.totalUserPrivileges.forEach(info => {
					info.privilegeName = info.admin ? 'Admin' : info.member ? 'Member' : 'None';
				});
			});
		}
	},
	mounted() {
		this.getData(() => {
			this.totalUserPrivileges = this.userInfoData.totalUserPrivileges;
		});
	},
	methods: {
		async getData(callBack) {
			const userInfoResult = await this.userInfoReq.read({ userId: this.userInfo.userId }, true);

			if (`ok` !== userInfoResult.data.result) {
				this.$alert({ type: 'warn', message: this.$t('msg.load.data.warn') });
				return;
			}

			this.userInfoData = userInfoResult.data.object;

			if (callBack) callBack();
		},
		changeTab(idx) {
			this.selectedIdx = idx;
		},
		async updateUserInfo() {
			let saveUserInfo = cloneDeep(this.userInfoData);

			this.$refs.component.forEach(comp => {
				saveUserInfo = merge(saveUserInfo, comp.getData());
			});

			saveUserInfo.passwordOld = encryptPassword(saveUserInfo.passwordOld);
			saveUserInfo.passwordNew = encryptPassword(saveUserInfo.passwordNew);

			const result = await this.userInfoReq.update(saveUserInfo, true);

			if ('ok' === result.data.result) {
				this.modalMode = 'detail';
				this.userInfoData.passwordOld = ' ';
				this.userInfoData.passwordNew = ' ';
				this.$alert({ type: 'normal', message: this.$t('msg.alert.update') });
				this.getData();
			} else {
				this.$alert({ type: 'warn', message: result.data.error[0].message });
			}
		},
		chgModalMode(modalMode) {
			this.modalMode = modalMode;
		}
	},
	data() {
		return {
			modalMode: 'detail',
			selectedIdx: 0,
			showModal: false,
			backdropMode: 'full',
			userInfoReq: new HttpReq(`/api/entity/user/object`),
			userInfoData: {},
			totalUserPrivileges: [],
			isSystem: 'S',
			isBusiness: 'b',
			tabList: [
				{
					type: 'basic',
					label: this.$t('label.basicInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'userId',
									label: this.$t('label.user.id'),
									isPK: true
								},
								{
									type: 'text',
									vModel: 'userName',
									label: this.$t('label.user.name'),
									isRequired: true,
									regExpType: 'name'
								},
								{
									type: 'text',
									vModel: 'department',
									label: this.$t('label.user.department')
								},
								{
									type: 'text',
									vModel: 'telephone',
									label: this.$t('label.telephone.number'),
									regExpType: 'num'
								}
							],
							[
								{
									type: 'text',
									vModel: 'mobilephone',
									label: this.$t('label.mobile.phone.number'),
									regExpType: 'num'
								},
								{
									type: 'text',
									vModel: 'email',
									label: this.$t('label.email')
								},
								{
									type: 'text',
									vModel: 'updateUserId',
									label: this.$t('label.modifier'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'updateTimestamp',
									label: this.$t('label.modified.time'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				},
				{
					id: 'Authentication',
					type: 'basic',
					label: this.$t('label.authenticationInfo'),
					content: [
						[
							[
								{
									type: 'select',
									vModel: 'userDisableYn',
									label: this.$t('label.disabled'),
									optionInfo: {
										url: `/api/page/properties`,
										params: {
											pk: {
												propertyId: 'List.Yn'
											},
											orderByKey: true
										},
										optionListName: 'openWindowList',
										optionFor: 'option in openWindowList',
										optionValue: 'option.pk.propertyKey',
										optionText: 'option.propertyValue'
									},
									readonly: true
								},
								{
									type: 'text',
									vModel: 'loginIp',
									label: this.$t('label.last.login.address'),
									readonly: true
								},
								{
									type: 'password',
									vModel: 'passwordOld',
									label: this.$t('label.user.originPassword'),
									regExpType: 'password'
								},
								{
									type: 'password',
									vModel: 'passwordNew',
									label: this.$t('label.user.newPassword'),
									regExpType: 'password'
								}
							],
							[
								{
									type: 'text',
									vModel: 'passwordExpiration',
									label: this.$t('label.user.passwordExpiration'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'userExpiration',
									label: this.$t('label.user.userExpiration'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								},
								{
									type: 'text',
									vModel: 'loginAttempts',
									label: this.$t('label.login.count'),
									readonly: true
								},
								{
									type: 'text',
									vModel: 'loginTimestamp',
									label: this.$t('label.last.login.timestamp'),
									readonly: true,
									formatter: value => changeDateFormat(value)
								}
							]
						]
					]
				},
				{
					type: 'checkboxProperty',
					label: this.$t('label.user.privilege.info')
				}
			]
		};
	}
};
</script>
