<script>
import Basic from '@/components/main/Basic.vue';
import { getPrivilege, changeDateFormat } from '@/utils/common.js';

export default {
	name: 'PublishLog',
	extends: Basic,
	created() {
		const privileges = getPrivilege(this.pageInfo.privilegeType);

		this.viewer = privileges.viewer;
		this.editor = privileges.editor;
	},
	mounted() {
		if (localStorage.getItem('searchObj')) {
			this.initNewTabSearchObj();
		}
	},
	data() {
		return {
			cudUrl: `/api/entity/publishLog/object`,

			//auth
			viewer: null,
			editor: null,

			//Search
			searchList: [
				{
					type: 'select',
					vModel: 'dateType',
					label: this.$t('label.date.type'),
					val: `P`,
					optionInfo: {
						optionFor: `option in [
							{value: 'P', text: '${this.$t(`label.publish`)}'}, 
							{value: 'M', text: '${this.$t(`label.migration`)}'}
						]`,
						optionValue: 'option.value',
						optionText: 'option.text',
						isViewAll: false
					}
				},
				{
					type: 'singleDaterange',
					vModel: 'fromDateTime',
					label: this.$t('label.from'),
					dateRangeType: 'from'
				},
				{
					type: 'singleDaterange',
					vModel: 'toDateTime',
					label: this.$t('label.to'),
					dateRangeType: 'to'
				},
				{
					type: 'select',
					vModel: 'resourceType',
					label: this.$t('label.publishLog.resource.type'),
					optionInfo: {
						optionFor: `option in [{value: 'R', text: '${this.$t('label.modelRecord')}'},
                                               {value: 'S', text: '${this.$t('label.service')}'},
                                               {value: 'I', text: '${this.$t('label.interface')}'}]`,
						optionValue: 'option.value',
						optionText: 'option.text'
					}
				},
				{
					type: 'text',
					vModel: 'resourceId',
					label: this.$t('label.publishLog.resource.id'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId'
				},
				{
					type: 'select',
					vModel: 'publishStatus',
					label: this.$t('label.publishStatus'),
					optionInfo: {
						optionFor: `option in [{value: 'A', text: '${this.$t('label.publishLog.status.active')}'},
                                               {value: 'D', text: '${this.$t('label.publishLog.status.done')}'},
                                               {value: 'F', text: '${this.$t('label.publishLog.status.fail')}'}]`,
						optionValue: 'option.value',
						optionText: 'option.text'
					}
				},
				{
					type: 'select',
					vModel: 'migrationStatus',
					label: this.$t('label.migration.status'),
					optionInfo: {
						optionFor: `option in [{value: 'I', text: '${this.$t('label.migration.init')}'},
											   {value: 'R', text: '${this.$t('label.migration.ready')}'},
											   {value: 'D', text: '${this.$t('label.migration.done')}'},
											   {value: 'C', text: '${this.$t('label.migration.cancel')}'}]`,
						optionValue: 'option.value',
						optionText: 'option.text'
					}
				},
				{
					type: 'modal',
					vModel: 'publishUserId',
					label: this.$t('label.publishLog.user'),
					placeholder: this.$t('msg.enterId'),
					regExpType: 'searchId',
					modalInfo: {
						title: this.$t('label.publishLog.user'),
						search: {
							list: [
								{
									type: 'text',
									vModel: 'userId',
									label: this.$t('label.id'),
									placeholder: this.$t('msg.enterId'),
									regExpType: 'searchId'
								},
								{
									type: 'text',
									vModel: 'userName',
									label: this.$t('label.name'),
									placeholder: this.$t('msg.enterName'),
									regExpType: 'name'
								},
								{
									type: 'dataList',
									vModel: 'pageSize',
									label: this.$t('label.listCnt'),
									placeholder: 'msg.coditionList',
									val: '10',
									optionInfo: {
										optionFor: 'option in [10, 100, 1000]',
										optionValue: 'option',
										optionText: 'option'
									}
								}
							]
						},
						button: {
							list: [{ id: 'initialize', isUse: true }]
						},
						grid: {
							url: `/api/entity/user/search`,
							totalCntUrl: `/api/entity/user/count`,
							paging: {
								isUse: true,
								side: 'server'
							},
							options: {
								columns: [
									{
										header: this.$t('label.id'),
										name: 'userId',
										width: '30%'
									},
									{
										header: this.$t('label.name'),
										name: 'userName',
										width: '40%'
									},
									{
										header: this.$t('label.user.department'),
										name: 'department',
										width: '30%'
									}
								]
							}
						},
						rowClickedCallback: ({ userId }) => userId
					}
				},
				{ type: 'text', vModel: 'publishMessage', label: this.$t(`label.publishMessage`), placeholder: this.$t('msg.enterData') },
				{
					type: 'dataList',
					vModel: 'pageSize',
					label: this.$t('label.listCnt'),
					placeholder: this.$t('msg.coditionList'),
					val: '10',
					optionInfo: {
						optionFor: 'option in [10, 100, 1000]',
						optionValue: 'option',
						optionText: 'option'
					}
				}
			],

			//Button
			buttonList: [
				{ id: 'migration', isUse: true },
				{ id: 'initialize', isUse: true },
				{ id: 'refresh', isUse: true },
				{ id: 'newTab', isUse: true },
				{ id: 'download', isUse: true, downloadUrl: `/api/entity/publishLog/download`, title: `PublishLog` }
			],

			// Grid
			gridInfo: {
				url: `/api/entity/publishLog/search`,
				totalCntUrl: `/api/entity/publishLog/count`,
				paging: {
					isUse: true,
					side: 'server'
				},
				options: {
					rowHeaders: ['checkbox'],
					columns: [
						{
							header: this.$t('label.publishLog.resource.type'),
							name: 'resourceType',
							align: 'center',
							formatter: info => {
								switch (info.row.resourceType) {
									case 'R':
										return this.$t('label.modelRecord');
									case 'S':
										return this.$t('label.service');
									case 'I':
										return this.$t('label.interface');
								}
							}
						},
						{
							header: this.$t('label.publishLog.resource.id'),
							name: 'resourceId'
						},
						{
							header: this.$t('label.publishStatus'),
							name: 'publishStatus',
							align: 'center',
							formatter: info => {
								switch (info.row.publishStatus) {
									case 'A':
										return this.$t('label.publishLog.status.active');
									case 'D':
										return this.$t('label.publishLog.status.done');
									case 'F':
										return this.$t('label.publishLog.status.fail');
								}
							}
						},
						{
							header: this.$t('label.publishLog.user'),
							name: 'publishUserId'
						},
						{
							header: this.$t('label.publishMessage'),
							name: 'publishMessage'
						},
						{
							header: this.$t('label.migration.status'),
							name: 'migrationStatus',
							align: 'center',
							formatter: info => {
								switch (info.row.migrationStatus) {
									case 'I':
										return this.$t('label.migration.init');
									case 'R':
										return this.$t('label.migration.ready');
									case 'D':
										return this.$t('label.migration.done');
									case 'C':
										return this.$t('label.migration.cancel');
								}
							}
						},
						{
							header: this.$t('label.migration.time'),
							name: 'migrationDateTime',
							formatter: obj => changeDateFormat(obj.value)
						}
					]
				}
			},
			gridSearchObj: {},
			gridType: 'mainBasic',

			//Detail
			detailPk: ['pk.publishId', 'pk.publishDateTime'],
			detailInfo: {},
			isDetailView: false,
			detailMode: null,
			detailTabList: [
				{
					type: 'basic',
					label: this.$t('label.basicInfo'),
					content: [
						[
							[
								{
									type: 'text',
									vModel: 'resourceType',
									label: this.$t('label.publishLog.resource.type'),
									formatter: info => {
										switch (info) {
											case 'R':
												return this.$t('label.modelRecord');
											case 'S':
												return this.$t('label.service');
											case 'I':
												return this.$t('label.interface');
										}
									}
								},
								{
									type: 'text',
									vModel: 'resourceId',
									label: this.$t('label.publishLog.resource.id')
								},
								{
									type: 'text',
									vModel: 'pk.publishId',
									label: this.$t('label.id')
								}
							],
							[
								{
									type: 'select',
									vModel: 'publishStatus',
									label: this.$t('label.publishStatus'),
									optionInfo: {
										optionFor: `option in [{value: 'A', text: '${this.$t('label.publishLog.status.active')}'},
                                              			   {value: 'D', text: '${this.$t('label.publishLog.status.done')}'},
                                               			   {value: 'F', text: '${this.$t('label.publishLog.status.fail')}'}]`,
										optionValue: 'option.value',
										optionText: 'option.text'
									}
								},
								{
									type: 'text',
									vModel: 'publishUserId',
									label: this.$t('label.publishLog.user')
								},
								{
									type: 'text',
									vModel: 'pk.publishDateTime',
									label: this.$t('label.publishLog.date'),
									formatter: value => changeDateFormat(value)
								}
							],
							[
								{
									type: 'select',
									vModel: 'migrationStatus',
									label: this.$t('label.migration.status'),
									optionInfo: {
										optionFor: `option in [{value: 'I', text: '${this.$t('label.migration.init')}'},
											   {value: 'R', text: '${this.$t('label.migration.ready')}'},
											   {value: 'D', text: '${this.$t('label.migration.done')}'},
											   {value: 'C', text: '${this.$t('label.migration.cancel')}'}]`,
										optionValue: 'option.value',
										optionText: 'option.text'
									}
								},
								{
									type: 'text',
									vModel: 'migrationDateTime',
									label: this.$t('label.migration.time'),
									formatter: value => changeDateFormat(value)
								}
							]
						],
						[
							[
								{
									type: 'textarea',
									vModel: 'publishMessage',
									label: this.$t('label.publishMessage'),
									height: `150px`
								}
							]
						]
					]
				},
				{
					type: 'property',
					label: this.$t('label.publishTargetInfo'),
					dataKey: 'publishTargets',
					content: [
						{
							type: 'text',
							vModel: 'pk.targetId',
							label: this.$t('label.publishLog.target.id')
						},
						{
							type: 'text',
							vModel: 'pk.targetTypeName',
							label: this.$t('label.type')
						},
						{
							type: 'text',
							vModel: 'updateTimestamp',
							label: this.$t('label.publishTimestamp')
						},
						{
							type: 'text',
							vModel: 'publishCode',
							label: this.$t('label.publishCode')
						},
						{
							type: 'text',
							vModel: 'publishResult',
							label: this.$t('label.publishResult')
						},
						{
							type: 'text',
							vModel: 'publishDoneName',
							label: this.$t('label.publishLog.target.done')
						}
					],
					setPropertyCallBack: propertyList => {
						propertyList.forEach(property => {
							property.pk.targetTypeName = `I` === property.pk.targetType ? this.$t(`label.server`) : this.$t(`label.system`);
							property.publishDoneName =
								`Y` === property.publishDone
									? this.$t(`label.yes`)
									: `N` === property.publishDone
									? this.$t(`label.no`)
									: `S` === property.publishDone
									? `Skip`
									: `Error`;
							if (property.updateTimestamp) property.updateTimestamp = changeDateFormat(property.updateTimestamp);
						});

						return propertyList;
					}
				}
			]
		};
	}
};
</script>
